////////// STANDARD STYLING //////////

* {
    box-sizing: border-box;
}


body {
    margin: 0;
    padding: 0;
    background: linear-gradient(to bottom, #d2d4c9,  #d4c9cf);
    background-attachment: fixed;
    background-size: 100% 500%; /* Adjust the size as needed */
}


section {
    display: flex;
    height: 100vh;
    display: grid;
    place-content: center;
    padding: 10em;
}

.image-center {
  display: flex;
  justify-content: center;
  margin-bottom: 20px; /* Add margin to create space between the image and the paragraph */
}

.blurb-text{
  font-size: 2rem;
  text-align: center;
  padding-left: 120px;
  padding-right: 120px;

}

.footer-fade{
  opacity: 0;
}
.fadein {
  opacity: 1;
  transition: opacity 4s ;
}






